*, ::after, ::before {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: black;
}

.white-text {
    font-weight: bold;
    font-family: cursive;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: dimgray;
    color: teal;
    font-size: 20px;
}

.navbar-links ul {
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar-links li {
    list-style: none;
}

.navbar-links li a {
    float: none;
    font-variant-caps: normal;
    font-family: cursive;
    color: white;
    padding: 1rem;
    display: block;
}

.navbar-links li:hover {
    background-color: orangered;
}

.toggle-button {
    position: absolute;
    top: .75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 19px;
}

.toggle-button .bar {
    color: rgba(0, 0, 0, .5);
    border-color: rgba(0, 0, 0, .1);
    height: 2px;
    width: 100%;
    background-color: white;
    border-radius: 10px;
}

@media (max-width: 5000px) {
    .brand-title {
        font-size: 1rem;
    }

    .toggle-button {
        display: flex;
    }

    .navbar-links {
        display: none;
        width: 100%;
    }

    .navbar {
        flex-direction: column;
        align-items: flex-start;
    }

    .navbar-links ul {
        width: 100%;
        flex-direction: column;
    }

    .navbar-links li {

        text-align: center;
    }

    .navbar-links li a {
        text-decoration: white;
        padding: .3rem 0.5rem;
    }

    .navbar-links.active {
        display: flex;
    }
}